import React from "react";
import { Helmet, HelmetProvider } from "react-helmet-async";
import { NavLink } from 'react-router-dom';

const Error = () => {
    return (
        <>
            <HelmetProvider>
                <Helmet>
                    <title>Запчасти ДТ | 404</title>
                    <meta name="description"
                        content='Страница не найдена' />
                </Helmet>
            </HelmetProvider>

            <div className="breadcrumb-section">
                <div className="breadcrumb-wrapper">
                    <div className="container">
                        <div className="row">
                            <div className="col-12 d-flex justify-content-between justify-content-md-between  align-items-center flex-md-row flex-column">
                                <h3 className="breadcrumb-title">404 Ошибка</h3>
                                <div className="breadcrumb-nav">
                                    <nav aria-label="breadcrumb">
                                        <ul>
                                            <li><NavLink to="/">Главная</NavLink></li>
                                            <li className="active">404</li>
                                        </ul>
                                    </nav>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="error-section">
                <div className="container">
                    <div className="row">
                        <div className="error_form">
                            <h1>404</h1>
                            <h4>Уупс! СТРАНИЦА НЕ НАЙДЕНА</h4>
                            <p>Извините, но страница, которую вы ищете, не существует, либо <br /> была удалена или временно недоступна.</p>
                            <div className="row">
                                <div className="col-10 offset-1 col-md-6 offset-md-3">
                                    <NavLink to="/">Вернуться на главную страницу</NavLink>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Error;