import React from "react";
import { Helmet, HelmetProvider } from "react-helmet-async";
import { NavLink } from "react-router-dom";

const Privacy = () => {
    return (
        <>
            <HelmetProvider>
                <Helmet>
                    <title>Запчасти ДТ | Политика конфиденциальности</title>
                    <meta name="description"
                        content='Самый большой ассортимент запасных частей на грузовые автомобили Маз, ЗИЛ, УРАЛ по самым выгодным ценам с доставкой по всей России "Запчасти ДТ" Набережные Челны.' />
                </Helmet>
            </HelmetProvider>
            <div className="breadcrumb-section">
                <div className="breadcrumb-wrapper">
                    <div className="container">
                        <div className="row">
                            <div className="col-12 d-flex justify-content-between justify-content-md-between  align-items-center flex-md-row flex-column">
                                <h3 className="breadcrumb-title" style={{ textAlign: 'center' }}>Политика конфиденциальности</h3>
                                <div className="breadcrumb-nav">
                                    <nav aria-label="breadcrumb">
                                        <ul>
                                            <li><NavLink to="/">Главная</NavLink></li>
                                            <li className="active" aria-current="page">Политика конфиденциальности</li>
                                        </ul>
                                    </nav>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="privacy-section">
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <div className="privacy-policy-wrapper">
                                <div className="privacy-single-item">
                                    <p>Настоящая Политика конфиденциальности (далее — Политика) является приложением к Пользовательскому соглашению и определяет порядок обработки и защиты персональной информации о Пользователях, которую Общество с ограниченной ответственностью «Манн, Иванов и Фербер» (далее — «Администрация»), может получить во время использования ими Cервиса Администрации (далее — Сервисы). </p>
                                    <p>Перед использованием Сервиса, пользователям следует ознакомиться с условиями настоящей Политики конфиденциальности.</p>
                                </div>
                                <div className="privacy-single-item">
                                    <h3>1. Общие положения</h3>
                                    <p>1.1. Использование Сервиса в любой форме означает безоговорочное согласие Пользователя с условиями настоящей Политики конфиденциальности и указанными в ней условиями обработки его персональной информации. В случае несогласия с условиями Политики конфиденциальности Пользователь должен воздержаться от использования Сервиса.</p>
                                    <p>1.2. Политика конфиденциальности (в том числе любая из ее частей) может быть изменена Администрацией без какого-либо специального уведомления и без выплаты какой-либо компенсации в связи с этим. Новая редакция Политики конфиденциальности вступает в силу с момента ее размещения на сайте Администрации.</p>
                                    <p>1.3. Принимая условия настоящей Политики, Пользователь выражает свое согласие на обработку Администрацией данных о Пользователе в целях, предусмотренных настоящей Политикой, а также на передачу данных о Пользователе третьим лицам в случаях, перечисленных в настоящей Политике.</p>
                                    <p>Указанное согласие может быть отозвано Пользователем только при условии письменного уведомления им Администрации не менее чем за 180 дней до предполагаемой даты прекращения использования данных Администрацией.</p>
                                    <p>Использование Сервиса с помощью веб-браузера, который принимает данные из cookies, означает выражение согласия Пользователя с тем, что Администрация может собирать и обрабатывать данные из cookies в целях, предусмотренных настоящей Политикой, а также на передачу данных из cookies третьим лицам в случаях, перечисленных в настоящей Политике.</p>
                                    <p>Отключение и/или блокировка Пользователем опции веб-браузера по приему данных из cookies означает запрет на сбор и обработку Администрацией данных из cookies в соответствии с условиями настоящей Политики конфиденциальности.</p>
                                    <p>1.4. По общему правилу Администрация не проверяет достоверность предоставляемой Пользователями персональной информации. Вместе с тем в случаях, предусмотренных Пользовательским соглашением, Пользователь обязан предоставить подтверждение достоверности предоставленной им персональной информации о себе.</p>
                                </div>
                                <div className="privacy-single-item">
                                    <h3>2. Состав информации о Пользователях, которую получает и обрабатывает Администрация</h3>
                                    <p>2.1.Настоящая Политика распространяется на следующие виды персональной информации:</p>
                                    <p>2.1.1. Персональная информация, размещаемая Пользователями, в т.ч. о себе самостоятельно при заполнении формы отправки сообщения, иная персональная информация, доступ к которой Пользователь предоставляет Администрации через веб-сайты или сервисы третьих лиц, или персональная информация, размещаемая Пользователями в процессе использования Сервиса. К персональной информации, полученной таким образом, могут относиться, в частности, фамилия, имя, номер телефона, адрес электронной почты Пользователя, адрес для доставки заказа. Иная информация предоставляется Пользователем на его усмотрение.</p>
                                    <p>Запрещается предоставление Пользователем персональных данных третьих лиц без полученного от третьих лиц разрешения на такое распространение либо, если такие персональные данные третьих лиц не были получены самим Пользователем из общедоступных источников информации.</p>
                                    <p>2.1.2. Настоящая Политика распространяет свое действие так же на кандидатов на имеющиеся вакансии Администрации, наряду с иными Пользователями. Кандидаты на вакансии, отправляя резюме Администрации с помощью Сервиса, либо по электронной почте, с целью прохождения собеседования и дальнейшего трудоустройства, выражают таким образом согласие на обработку следующих персональных данных: фамилия, имя, отчество, дата рождения, гражданство, город проживания, контакты (телефон, адрес электронной почты), места работы и даты работы, а также иных данных, указанных кандидатами на вакансии в резюме.</p>
                                    <p>2.1.3. Продавец гарантирует Покупателю сохранение конфиденциальности следующей персональной информации о Покупателе:</p>
                                    <p>— информация о карте пользователя (последние 4 цифры);</p>
                                    <p>— сведения о покупках и заказах.</p>
                                    <p>Указанная информация передается Продавцом третьим лицам исключительно с целью осуществления оплаты заказа платежной системой; иные случаи передачи указанной информации третьим лицам не допускаются.</p>
                                    <p>2.1.4. Данные, автоматически передаваемые Сервису в процессе их использования с помощью установленного на устройстве Пользователя программного обеспечения, в т.ч. IP-адрес, индивидуальный сетевой номер устройства (MAC-адрес, ID устройства), электронный серийный номер (IMEI, MEID), данные из cookies, информация о браузере, операционной системе, времени доступа, поисковых запросах Пользователя.</p>
                                    <p>2.1.5. Данные, дополнительно предоставляемые Пользователями по запросу Администрации в целях выполнения обязательств Администрации перед Пользователями в отношении использования Сервиса.</p>
                                    <p>2.1.6. Иная информация о Пользователях, сбор и/или обработка которой установлены пользовательским соглашением Администрации.</p>
                                </div>
                                <div className="privacy-single-item">
                                    <h3>3. Цели сбора и обработки информации о Пользователях</h3>
                                    <p>3.1. Администрация осуществляет сбор и обработку только той информации о Пользователях, в т.ч. их персональных данных, которая необходима для выполнения обязательств Администрации по предоставлению Сервиса, ответа на вопрос, заданный Пользователем при отправке сообщения с помощью Сервиса, а также исполнения обязательств, предусмотренных пользовательским соглашением.</p>
                                    <p>3.2. Администрация может использовать персональную информацию Пользователей для целей:</p>
                                    <p>3.2.1. идентификации стороны в рамках договоров между Пользователем и Администрацией.</p>
                                    <p>3.2.2.предоставления Пользователям услуг с использованием Сервиса и для выполнения своих обязательств перед ними, в т.ч. уточнения данных платежа, обработки заказов и запросов и дальнейшего совершенствования Сервиса, разработки новых сервисов и услуг.</p>
                                    <p>3.2.3. информирования Пользователей о появлении новых материалов на Сайте, направления запросов, касающихся использования Сервиса, обратной связи с Пользователем.</p>
                                    <p>3.2.4. выполнения маркетинговых задач, проведения статистических и иных исследований на основе обезличенных данных,</p>
                                    <p>3.2.5. информирования Пользователя посредством электронных рассылок. Предоставляя свои данные, Пользователь соглашается на получение сообщений рекламно-информационного характера и сервисных сообщений (рассылку).</p>
                                    <p>3.3. Целями обработки персональных данных кандидатов на вакансии являются:</p>
                                    <p>— Обеспечение соблюдения требований законодательства РФ.</p>
                                    <p>— Решение вопросов трудоустройства, оформление и регулирование трудовых отношений.</p>
                                    <p>— Отражение информации в кадровых документах.</p>
                                    <p>— Иные цели обработки ПД могут быть утверждены приказом Оператора.</p>
                                    <p>3.4. Мобильные приложения могут собирать анонимные данные о местоположении пользователя, для того чтобы обеспечить более корректную работу с выбором способа платежа. Мобильные приложения могут собирать анонимную статистику использования.</p>
                                    <p>3.5. Настоящим Пользователь выражает свое согласие на передачу персональной информации о нем партнерам Администрации, третьим лицам в целях, предусмотренных п. 3.2 настоящей Политики конфиденциальности.</p>
                                    <p>3.6. При необходимости использовать персональную информацию о Пользователе в целях, не предусмотренных настоящей Политикой, Администрация запрашивает согласие Пользователя на такие действия.</p>
                                </div>
                                <div className="privacy-single-item">
                                    <h3>4. Обработка информации о Пользователях</h3>
                                    <p>4.1. Персональная информация о Пользователях хранится в соответствии с действующим законодательством.</p>
                                    <p>4.2. Персональная информация о Пользователях не передается третьим лицам, за исключением следующих случаев:</p>
                                    <p>4.2.1. Пользователь выразил согласие на такие действия.</p>
                                    <p>4.2.2. Передача необходима в целях обеспечения функционирования Сервиса и/или его отдельных функциональных возможностей.</p>
                                    <p>4.2.3. Передача предусмотрена применимым правом.</p>
                                    <p>4.2.4. В целях обеспечения возможности защиты прав и законных интересов Администрации и/или третьих лиц в случаях, когда Пользователь нарушает условия пользовательского соглашения.</p>
                                    <p>4.2.5. Если Администрация примет участие в слиянии, приобретении или любой другой форме продажи части или всех своих активов. При этом к приобретателю активов Администрации переходят все обязательства по соблюдению условий настоящей Политики.</p>
                                    <p>4.3. Пользователь настоящим уведомлен и соглашается, что Администрация может получать персональные данные третьих лиц, которые предоставляются Пользователем при использовании Сервиса и использовать их для реализации отдельных функций Сервиса, при условии, что Пользователь гарантирует наличие согласия третьих лиц, данные о которых предоставляются Пользователем при использовании Сервиса, на обработку Администрацией, в целях, предусмотренных настоящей Политикой, а также на передачу таких данных в случаях, перечисленных в настоящей Политике.</p>
                                    <p>4.4. Кроме того, Пользователь настоящим уведомлен и соглашается, что Администрация может получать статистические обезличенные (без привязки к Пользователю) данные о действиях Пользователя при использовании Сервиса.</p>
                                    <p>4.5. Пользователи вправе по запросу получать от Администрации информацию, касающуюся обработки их персональных данных.</p>
                                </div>
                                <div className="privacy-single-item">
                                    <h3>5. Меры по защите информации о Пользователях</h3>
                                    <p>5.1. Администрация принимает все необходимые и достаточные организационные и технические меры для защиты персональной информации о Пользователях от неправомерного или случайного доступа к ним, уничтожения, изменения, блокирования, распространения персональной информации, а также от иных неправомерных действий с ней. К этим мерам относятся, в частности, внутренняя проверка процессов сбора, хранения и обработки данных и мер безопасности, включая меры по обеспечению физической безопасности данных для предотвращения неавторизированного доступа к персональной информации.</p>
                                    <p>5.2. При обработке персональных данных Пользователей Администрация руководствуется Федеральным законом <a href="http://www.consultant.ru/document/cons_doc_LAW_61801/" target="_blank" rel="nofollow noopener" style={{ color: '#ea1c26', opacity: 0.75 }}>«О персональных данных»</a> от 27.07.2006 г. № 152-ФЗ.</p>
                                </div>
                                <div className="privacy-single-item">
                                    <h3>6. Заключительные положения</h3>
                                    <p>6.1. Настоящая Политика, отношения между Пользователем и Администрацией, возникающие в связи с применением настоящей Политики, а также вопросы, не урегулированные настоящей Политикой, регулируются действующим законодательством Российской Федерации.</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Privacy;